.image-container {
    position: relative;
    
  }
  
  .text-overlay {
    position: absolute;
    top: 25%;
    left: 1.5%;
    transform: translate(-20%, -50%);
    background-color: rgba(255, 255, 255, 0.95); /* Change the color and opacity as per your need */
    color: rgb(0, 0, 0); /* Change the text color as per your need */
    padding: 5px 10px;
    border-radius: 10px;
    text-align: center;
  }
  .title-cov{
    transform: translate(-0%, -90%);
  }
  .description-cov{
    width:30%;
    height:40%;
    transform: translate(-0%, 10%);
    text-align:left;
    background-color:rgba(255, 255, 255, 0);

  }
  .links-cov {
    width:50%;
    height:15%;
    background-color:rgba(240, 248, 255, 0);
    transform: translate(-0%, 280%);
    text-align:left;
  }