.container {
    position: relative;
    display: inline-block;
  }
  
  .links {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(159, 159, 159, 0.681);
    color: white;
    text-align: center;
    padding: 10px;
    box-sizing: border-box;
    opacity: 0;
    transition: opacity 0.2s ease-in-out;
  }
  .circle {
    width:4.5em;
    height: 4.5em;
    border-radius: 50%;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom:0;
    transition-duration: 0.2s;
  }
  .circle :hover {
    transition-duration: 0.2s;
    /* background-color:rgb(78, 78, 78); */
    width:4.5em;
    height: 4.5em;
    /* border-radius: 50%; */

  }
  .container:hover .links {
    opacity: 1;
  }
  .rounded-link {
    border-radius:100000px;
    background-color:"white"
  }
  .imglink {
    width: 80%;
    height: auto;
  }